// src/pages/CrawlerTaskList.js
import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { getToken, getCrawlerTask } from '../services/auth';

export default function CrawlerTaskList() {
    const [tasks, setTasks] = useState([]);
    const [error, setError] = useState('');

    useEffect(() => {
        const fetchTasks = async () => {
            try {
                const response = await getCrawlerTask({ token: getToken() });
                setTasks(response.data.$values || []);
            } catch (err) {
                setError(' ');
            }
        };

        fetchTasks();
    }, []);

    return (
        <div className="container py-5">
            <div className="d-flex justify-content-between align-items-center mb-4">
                <h2 className="fw-bold"></h2>
                {/*  */}
                <Link to="/crawlerrequest" className="btn btn-outline-primary">
                    
                </Link>
            </div>
            <div className="card shadow-sm">
                <div className="card-body">
                    <h3 className="mb-4 fw-bold"> </h3>

                    {error && <div className="alert alert-danger">{error}</div>}

                    {tasks.length === 0 && !error ? (
                        <div className="alert alert-info text-center"></div>
                    ) : (
                        <div className="table-responsive">
                            <table className="table table-hover align-middle">
                                <thead className="table-dark">
                                    <tr>
                                        <th scope="col"></th>
                                        <th scope="col"></th>
                                        <th scope="col"></th>
                                        <th scope="col"></th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {tasks.map((task) => (
                                        <tr key={task.id}>
                                            <td className="fw-bold">{task.taskId}</td>
                                            <td>
                                                <code>{task.source}</code>
                                            </td>
                                            <td>
                                                <span
                                                    className={`badge ${
                                                        task.status === 'Received'
                                                            ? 'bg-secondary'
                                                            : task.status === 'Processing'
                                                            ? 'bg-warning text-dark'
                                                            : task.status === 'Completed'
                                                            ? 'bg-success'
                                                            : 'bg-dark'
                                                    }`}
                                                >
                                                    {task.status}
                                                </span>
                                            </td>
                                            <td>{new Date(task.createdAt).toLocaleString()}</td>
                                        </tr>
                                    ))}
                                </tbody>
                            </table>
                        </div>
                    )}
                </div>
            </div>
        </div>
    );
}
